	body{
		margin:0;
		left: 0;
		top:0;
		overflow:hidden;
		font-family: "Roboto", sans-serif;
	}
	.svg{
		width: 100%;
		position: fixed;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		    -ms-transform: translate(-50%, -50%);
		        transform: translate(-50%, -50%);
		}
	
	.Homepage{
		margin: 0;
		left:0;
		top:0;
		width:100%;
		height: 100vh;
		background: cover;

		background-color: #A08A58;
	
	}
	.start_btn{
		margin-top: 9999;
        cursor: pointer;
	}
	#pzda{
		position: absolute;
		z-index: 9999;
	}
	.st0{display:none;}
	.st1{display:inline;fill:#A08A58;}
	.st2{display:inline;fill:#5B554D;}
	.st3{display:inline;fill:#FFF6E3;}
	.st4{display:inline;fill:#BAA478;}
	.st5{display:inline;fill:#E5C875;}
	.st6{fill:#A08A58;
        cursor: pointer;
    }
	.st7{fill:#E5C875;}
	.st8{fill:#FFF6E3;}
	.st10{font-size:54.7786px;}
	.st11{fill:#BAA478;}
	.st13{font-size:32.8671px;}
	.st14{fill:#F4EBD4;}
	.st15{display:inline;}
	.st16{font-size:60px;}
	.st17{fill:#5B554D;}
	.st18{font-size:36px;}
	.st20{font-size:24px;}
	.st21{fill:#FFF6E3;stroke:#FFF6E3;stroke-miterlimit:10;}


