body {
  font-family: "Roboto", sans-serif;
  overflow: hidden; }

.Mainpage {
  background-color: #FFF6E3;
  width: 100vw;
  height: 100vh;
  margin-left: 0px;
  overflow: hidden; }

.main-row {
  height: 95vh; }

.collapse {
  display: none; }

.collapse.in {
  display: block; }

.map-column, .tour-column {
  padding: 0px;
  overflow: hidden; }

.tour-column {
  overflow: scroll;
  overflow-x: hidden;
  overflow-y: auto; }

.tour-column::-webkit-scrollbar {
  width: 0.5vmax; }

.tour-column::-webkit-scrollbar-thumb {
  background-color: #BAA478;
  border-radius: 0.2vmax; }

.single-tour-plan {
  width: 100%;
  height: 6vmax;
  cursor: pointer;
  color: #5B553D; }

.tour-plan-name {
  font-weight: 500;
  font-size: 1vmax;
  display: table-cell;
  vertical-align: middle; }

.att-name {
  color: #5B553D;
  font-size: 1vmax;
  display: table-cell;
  vertical-align: middle; }

.tour-plan-price {
  font-weight: 500;
  margin-left: 3vmax;
  border: 3px solid #5B553D;
  padding: 0.15vmax;
  border-radius: 0.5vmax;
  font-size: 1vmax; }

.tour-plan-img {
  width: 100%;
  height: 6vmax; }

.price_wrapper {
  width: 25%;
  float: left;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%); }

.name_wrapper {
  width: 47%;
  float: left;
  display: table;
  height: 100%; }

.img_wrapper {
  width: 28%;
  float: left; }

.name_wrapper_child {
  width: 72%;
  float: left;
  display: table;
  height: 100%; }

.img_wrapper_child {
  width: 28%;
  float: left; }

[data-odd-checker^="0"] {
  background-color: rgba(186, 164, 120, 0.2) !important; }

[data-odd-checker^="1"] {
  background-color: rgba(186, 164, 120, 0.4) !important; }

.answer_section {
  height: 7vmax; }

.main-footer {
  height: 5vh;
  margin: 0px;
  background-color: #BAA478;
  z-index: 10000; }

.footer-text, .copyright-text, .developed-text {
  font-size: 1vmax;
  color: #5B553D;
  font-weight: 500;
  line-height: 5vh; }

.copyright-text {
  float: left;
  padding-left: 4vmax; }

.developed-text {
  position: absolute;
  right: 4vmax; }

@media only screen and (max-width: 767px) {
  .main-row {
    height: 95vh; }
  .tour-column {
    height: 50vh; }
  .map-column {
    height: 38vh; }
  .main-footer {
    height: 5vh; } }



